ut[type=\"radio\"]:checked {\n  background-color: $primary-color;\n}\n\n.radio-component {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n  text-align: center;\n  cursor: pointer;\n  color: $black-color;\n  padding-right: 30px;\n\n  .radio-label {\n    font-size: 20px;\n    cursor: pointer;\n  }\n\n  // .......color.....\n\n  &.radio--secondary {\n    input[type=\"radio\"]:checked {\n      background-color: $secondary-color;\n    }\n    .radio-input {\n      border: 1px solid $secondary-color;\n    }\n  }\n\n  &.radio--danger {\n    input[type=\"radio\"]:checked {\n      background-color: $danger-color;\n    }\n    .radio-input {\n      border: 1px solid $danger-color;\n    }\n\n    .radio-label {\n      color: $danger-color;\n    }\n  }\n  &.radio--disable {\n    color: #dddddd !important;\n    cursor: context-menu;\n\n    input[type=\"radio\"]:checked {\n      background-color: #dddddd !important;\n    }\n\n    .radio-input {\n      border: 1px solid #dddddd;\n      cursor: context-menu;\n    }\n\n    .radio-label {\n      color: #dddddd;\n      cursor: context-menu;\n    }\n  }\n}\n\n.radio--error {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n\n  .radio-emsg {\n    color: #ff3333;\n    font-size: 13px;\n    margin-left: 6px;\n  }\n  .radio-emsg-disable {\n    color: #dddddd !important;\n  }\n}\n","$primary-color: #3563e9;\n$secondary-color: #eeeeee;\n$sucess-color: #66bb6a;\n$gray-color: #eeeeee;\n$black-color: #11151f;\n$white-color: #ffffff;\n$danger-color: #D20319 ;\n$light-color:#B8C3E5;\n\n\n$fontsize-small: 12px;\n$fontsize-medium: 14px;\n$fontsize-large: 16px;\n\n$up-arrow: url(\"../../images/up_arrow.png\");\n$down-arrow: url(\"../../images/down_arrow.png\");\n$default: url(\"../../images/default.png\");\n\n$up-accordion: url(\"../../images/up-arrow.svg\");\n$down-accordion: url(\"../../images/down-arrow.svg\");\n",".button-disabled {\n  background-color: none !important;\n}\n\n.button-component {\n  box-sizing: border-box;\n  di