\n  height: 82%;\n  z-index: 3;\n  pointer-events: none;\n}","@import \"../variables/variable.scss\";\n\n$table-font: Roboto, Helvetica, Arial, sans-serif;\n\n.table-caption {\n  padding-bottom: 15px;\n  font-family: $table-font;\n  width: 100%;\n  text-align: center;\n}\n.table-component {\n  width: 100%;\n  border-spacing: 0;\n  font-family: $table-font;\n\n  &.table-border {\n    border-spacing: 1px;\n  }\n  th {\n    padding: 15px;\n  }\n\n  thead {\n    th {\n      border-bottom: 1px solid rgb(224, 224, 224);\n      padding: 16px;\n      font-weight: 500;\n      font-size: 0.875rem;\n      text-align: left;\n\n      &.th-padding {\n        padding: 8px;\n      }\n      &.th-color {\n        background: $black-color;\n        color: $white-color;\n      }\n    }\n  }\n\n  th {\n    &.up {\n      background-image: $up-arrow;\n    }\n    &.down {\n      background-image: $down-arrow;\n    }\n    &.default {\n      background-image: $default;\n    }\n    &.up,\n    &.down,\n    &.default {\n      cursor: pointer;\n      background-repeat: no-repeat;\n      background-position: center right;\n    }\n  }\n\n  tbody {\n    tr {\n      td {\n        font-weight: 400;\n        font-size: 0.875rem;\n        border-bottom: 1px solid rgb(224, 224, 224);\n        padding: 15px;\n        color: rgba(0, 0, 0, 0.87);\n\n        &.td-border {\n          border: 0.5px solid rgb(224, 224, 224);\n        }\n\n        &.td-padding {\n          padding: 8px;\n        }\n      }\n    }\n  }\n  tbody {\n    tr:nth-child(even) {\n      .td-stripe {\n        background-color: #f6f6f6;\n\n        &.td-stripe:hover {\n          background-color: #e6e6e6;\n        }\n      }\n    }\n    tr {\n      td:hover {\n        background-color: #e6e6e6;\n      }\n    }\n  }\n}\n","@import \"../variables/variable.scss\";\n\n.switch {\n  position: relative;\n  display: inline-block;\n  width: 60px;\n  height: 34px;\n\n  .switch-label {\n    position: absolute;\n    top: 15px;\n    left: 59px;\n    color: #555;\n    font-size: 19px;\n  }\n\n  input {\n    opacity: 0;\n    width: 0;\n    height: 0;\n  }\n  .slider {\n    position: absolute;\n    cursor: pointer;\n    background-color: #bbb;\n    -webkit-transition: 0.4s;\n    transition: 0.4s;\n\n    &.sider-disable {\n      opacity: 0.4;\n      cursor: context-menu;\n    }\n\n    //..............size..........//\n    &.slider-small {\n      top: 24px;\n      left: 8px;\n      right: 25px;\n      bottom: 3px;\n    }\n    &.slider-medium {\n      top: 22.5px;\n      left: 6px;\n      right: 23px;\n      bottom: 3.25px;\n    }\n    &.slider-large {\n      top: 21.5px;\n      left: 3px;\n      right: 19px;\n      bottom: 2px;\n    }\n    //............................//\n  }\n  .slider:before {\n    position: absolute;\n    content: \"\";\n    background-color: white;\n    -webkit-transition: 0.4s;\n    transition: 0.4s;\n    box-shadow: rgba(60, 64, 67, 0.75) 0px 2px 3px 0px,\n      rgba(60, 64, 67, 0.75) 0px 2px 6px 1px;\n  }\n  .sider-disable:before {\n    background-color: #eee;\n  }\n  //............circle size............//\n  .slider-small:before {\n    height: 14px;\n    width: 14px;\n    left: 0px;\n    bottom: -3px;\n  }\n  .slider-medium:before {\n    height: 16px;\n    width: 16px;\n    left: 0px;\n    bottom: -3.25px;\n  }\n  .slider-large:before {\n    height: 20px;\n    width: 20px;\n    left: 0px;\n    bottom: -4.25px;\n  }\n  //.....................//\n  input:checked + .slider {\n    background-color: $primary-color;\n    opacity: 0.75;\n    &.slider:before {\n      background-color: $primary-color;\n    }\n    &.slider-secondary {\n      background-color: $secondary-color;\n\n      &.slider:before {\n        background-color: $secondary-color;\n      }\n    }\n    &.slider-success {\n      background-color: $sucess-color;\n\n      &.slider:before {\n        background-color: $sucess-color;\n      }\n    }\n    &.slider-danger {\n      background-color: $danger-color;\n      &.slider:before {\n        background-color: $danger-color;\n      }\n    }\n    &.sider-disable {\n      opacity: 0.4;\n    }\n\n    //..............size.....//\n    &.slider-small {\n      top: 24px;\n      left: 8px;\n      right: 25px;\n      bottom: 3px;\n    }\n    &.slider-small:before {\n      height: 14px;\n      width: 14px;\n      left: -12px;\n      bottom: -3px;\n    }\n    &.slider-medium {\n      top: 22.5px;\n      left: 6px;\n      right: 31px;\n      bottom: 3.25px;\n    }\n    &.slider-medium:before {\n      height: 16px;\n      width: 16px;\n      left: -10px;\n      bottom: -3.45px;\n    }\n    &.slider-large {\n      top: 21.5px;\n      left: 3px;\n      right: 30px;\n      bottom: 2px;\n    }\n\n    &.slider-large:before {\n      height: 20px;\n      width: 20px;\n      left: -9px;\n      bottom: -4.25px;\n    }\n  }\n  input:focus + .slider {\n    box-shadow: 0 0 1px $primary-color;\n    &.slider-secondary {\n      box-shadow: 0 0 1px $secondary-color;\n    }\n  }\n  input:checked + .slider:before {\n    -webkit-transform: translate(26px);\n    -ms-transform: translate(26px);\n    transform: translateX(26px);\n\n    &.slider-small {\n      top: 24px;\n      left: 8px;\n      right: 17px;\n      bottom: 1px;\n    }\n  }\n\n  // ....size.....\n\n  .round {\n    border-radius: 34px;\n  }\n  .round:before {\n    border-radius: 50%;\n  }\n}\n",".input-container {\n  display: flex;\n  z-index: 1;\n  position: relative;\n  flex-direction: column;\n  & .input-label {\n    font-size: 14px;\n    margin-bottom: 4px;\n  }\n\n  & .icon {\n    position: absolute;\n    right: 6px;\n    top: 6px;\n    z-index: 2;\n    object-fit: contain;\n    color: $primary-color;\n    font-size: 16px;\n  }\n\n  & .icon-start {\n    position: absolute;\n    left: 29px;\n    top: 7px;\n    z-index: 2;\n    object-fit: contain;\n    color: $primary-color;\n    font-size: 16px;\n  }\n\n\n  & .icon-end {\n    position: absolute;\n    right: 6px;\n    top: 6px;\n    z-index: 2;\n    object-fit: conta